const app = getApp();
import helper from '../common/helper.js';
import uniDrawer from '@/components/uni-drawer.vue'
export default {
	data() {
		return {
			indicatorDots: true,
			Inv: 0,
			list: [],
			keywords: '',
			navList: [],
			tabType: 'is_new',
			page_size: '10',
			page: '1',
			imgUrl: '',
			navLeft: [],
			navReft: [],
			active: '0',
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			},
			activeHead: '0',
			showLeft: false,
			classType: '0',
			bankId: '',
			letfNavChild: []
		};
	},
	components: {uniDrawer},
	onLoad() {
		this.getNav();
		// this.getTab();
		this.imgUrl = helper.imgUrl;
		this.getNavLeft()
		this.getReftNav()
	},
	methods: {
		// 轮播跳转
		navsHead(e) {
			console.log(e)
			uni.navigateTo({
				url: `/pages/allProduct?id=${e.currentTarget.dataset.id}&title=${e.currentTarget.dataset.title}`
			});
		},
		// 左边导航
		leftNav(index,id) {
			this.active = index;
			this.showLeft = true;
			this.bankId = id;
			this.page = '1';
			this.getLeftNavChild()
			console.log(id)
			console.log(this.showLeft)
		},
		// 左边导航数据
		getNavLeft(){
			uni.request({
				url: `${helper.requestUrl}/holes/banks`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res,'左边导航数据');
					if (res.data.status_code == 200) {
						this.navLeft = res.data.data;
						this.bankId = res.data.data[0].id
						// this.page = '1'
						// this.list = [];
						// this.getTab();
						
					} else {
						// uni.showToast({
						// 	title: res.data.message,
						// 	icon: 'none'
						// });
					}
				}
			});
		},
		childItem(bankId,childId,name){
			this.showLeft = false;
			uni.navigateTo({
				url: `/pages/indexA?bankId=${bankId}&name=${name}&childId=${childId}`
			})
		},
		// 获取银行子类
		getLeftNavChild(){
			uni.request({
				url: `${helper.requestUrl}/holes/bank_child`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res,'右边导航数据');
					if (res.data.status_code == 200) {
						this.letfNavChild = res.data.data;
					} else {
						
					}
				}
			});
		},
		// 右边导航数据
		getReftNav(){
			uni.request({
				url: `${helper.requestUrl}/holes/loan_class`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res,'右边导航数据');
					if (res.data.status_code == 200) {
						this.navReft = res.data.data;
						this.list = [];
						this.getTab();
					} else {
						// uni.showToast({
						// 	title: res.data.message,
						// 	icon: 'none'
						// });
					}
				}
			});
		},
		closeDrawer() {
			this.showLeft = false
		},
		clickItem(bankId){
			this.showLeft = false
			this.bankId = bankId
			this.page = '1';
			this.list = []
			this.getTab()
		},
		// 右边头部导航
		headNav(index,id) {
			console.log(index,id);
			this.activeHead = index;
			this.classType = id
			this.list = [];
			this.page = '1';
			this.getTab()
		},
		changeTab(e) {
			console.log(e);
			this.Inv = e;
			this.page = '1';
			if (this.Inv == 0) {
				// 最新
				this.tabType = 'is_new';
			} else if (this.Inv == 1) {
				// 热门
				this.tabType = 'is_hot';
			} else if (this.Inv == 3) {
				// 推荐
				this.tabType = 'is_romend';
			}
			this.list = [];
			this.getTab();
		},
		getSearch() {
			uni.navigateTo({
				url: '/pages/searchNetloan'
			});
		},
		//轮播导航
		getNav() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						let arr = res.data.data;
						let result = [];
						let num = 10;
						for (let i = 0; i < arr.length; i += num) {
							result.push(arr.slice(i, i + num));
						}
						console.log(result);
						this.navList = result;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 最新
		getTab() {
			uni.request({
				url: `${helper.requestUrl}/holes/index-recommends`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.tabType,
					bank_id: this.bankId,
					class_id: this.classType,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					} else {
						// uni.showToast({
						// 	title: res.data.message,
						// 	icon: 'none'
						// });
					}
				}
			});
		},
		goProduct(e) {
			console.log(e);
			let id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url: `/pages/productDetail?id=${id}`
			});
		},
		onReachBottom() {
			this.page++;
			this.getTab();
		}
	}
};
